@import url(https://fonts.googleapis.com/css?family=Overlock:400,400i,700|Oleo+Script);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  color: green;
  font-size: 50px;
}

*,
*::before,
*::after,
:root {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    height: 100%;
}

body {
    color: #252934;
    background: #fafafa;
    font-size: 62.5%;
    font-family: 'Overlock', Arial, Helvetica, sans-serif;
    overflow-x: hidden;
}

a,
a:visited {
    color: #252934;
    font-size: 1.4rem;
    text-decoration: none;
    transition: 200ms;
}

body {
  background-color: aquamarine;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.container {
    width: 750px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    padding-top: 12.5px;
    border: 1px solid;
    border-radius: 12.5px;
    background-color: #ffffff;
}

.user-avatar {
  min-width: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 50%;
    width: 50px;
    height: 50px;
}


.post-body {
  padding-right: 12.5px;
  font-size: 16px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding-bottom: 20px;
}
.user-info{
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: baseline;
          align-items: baseline;
  grid-gap: 15px;
  gap: 15px;
  width: 100%;
  font-family: 'Poppins', sans-serif;
}

.user-name {
  font-weight: bold;
}


.post-text {
  font-family: 'Poppins', sans-serif;
  margin-top: 15px;
  width: 100%;
}

.post-image {
  border-radius: 15px;
  max-width: 100%; 
  height: auto; 
  display: block;
  margin-top: 15px;
}


